import styled from 'styled-components';
import { ThemeProps } from '../../../theme/theme.config';

export const StyledSection = styled.section`
  width: 100%;
  display: flex;
  justify-content: space-between;
`;

export const TextContainer = styled.div`
  flex: 1;
`;

export const SectionTitle = styled.h2``;

export const SectionDescription = styled.p``;

export const SectionImage = styled.img`
  flex: 1;
`;

export const SectionNumber = styled.h3.attrs(
  ({
    containerHeight,
    active = false,
  }: {
    containerHeight: number;
    active?: boolean;
  }) => ({ containerHeight, active })
)`
  min-width: 70px;
  min-height: 70px;
  max-width: 70px;
  max-height: 70px;
  text-align: center;
  line-height: 70px;
  color: ${({ theme }: ThemeProps) => theme.color.white};
  background-color: '#EBEBEB';
  border-radius: 50%;
  margin: 0 20px;
  position: relative;
  font-size: 27px;

  ${(props) =>
    props.active &&
    `
  animation: setColor 1s 1 0s linear;
  `}

  @keyframes setColor {
    0% {
      background: #ebebeb;
    }
    100% {
      background: #044aaf;
    }
  }

  &::after {
    content: '';
    display: block;
    width: 6px;
    height: ${(props) => `${props.containerHeight - 70}px`};
    background-color: '#EBEBEB';
    position: absolute;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
  }
`;
